<template>
  <div id="index">
      <div class="index_Tab">
        <div class="Tab_content">
          <ul class="Index1">
            <router-link to="/myMusic/personality"><li>音乐</li></router-link>
            <router-link to="/myMusic/recommend"><li>推荐歌单</li></router-link>
            <router-link to="/myMusic/newMusic"><li>最新音乐</li></router-link>
            <router-link to="/myMusic/newMV"><li>最新MV</li></router-link>
          </ul>
        </div>
      </div>
      <div class="index_main">
        <router-view name="myIndex"></router-view>
      </div>
      <div class="player" ref="player">
        <div class="lock" ref="lock" @click="play">
          <div v-if="lock === true" class="icon iconfont icon-jiesuo"></div>
          <div v-else class="icon iconfont icon-suoding"></div>
        </div>
        <!-- autoplay自动播放 -->
        <div class="audio">
          <audio :src='musicUrl' controls autoplay></audio>
        </div>
    </div>
    <statement></statement>
  </div>
</template>

<script>
import statement from '../views/A.statement.vue'
export default {
  components: {
    statement
  },
  data () {
    return {
      lock: false,
      musicUrl: 'https://music.163.com/song/media/outer/url?id='
    }
  },
  methods: {
    play () {
      this.lock = !this.lock
      // 为锁定的时候
      if (this.lock === false) {
        this.$refs.player.style.transform = 'translateY(0)'
      } else {
        this.$refs.player.style.transform = 'translateY(60px)'
      }
    }
  }
}
</script>

<style lang="less">
#index{
  min-width: 100%;
  padding-top: 80px;
  /* tab栏 */
  .index_Tab{
    width: 100%;
    height: 34px;
    margin: 0 auto;
    background-color: #242424;
      .Tab_content{
        width: 744px;
        height: 100%;
        margin-left: 400px;
        /* background-color: pink; */
        ul{
          width: 500px;
          display: grid;
          grid-template-columns: 25% 25% 25% 25%;
        }
        .router-link-active li{
          background-color: rgb(165, 55, 76);
          border-radius: 20px;
        }
        a{
          cursor: pointer;
          font-size: 14px;
          font-weight: bold;
          color: #ffffff;
          opacity: 0.9;
          height: 34px;
          li{
            text-align: center;
            line-height: 34px;
            letter-spacing: 8px;
          }
        }
      }
  }
  /* 下部分播放器 */
  .player{
    position: fixed;
    height: 80px;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 66;
    /* transform: translateY(60px); */
    transition: all .5s ease;
    /* 锁头 */
    .lock{
      position: absolute;
      right: 8px;
      width: 40px;
      height: 0;
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px;
      border-bottom: 20px rgb(238, 180, 180) solid;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      cursor: pointer;
      box-shadow: 0 10px 2px rgb(134, 120, 120);
      // overflow: hidden;
      .icon{
        width: 20px;
        margin: 0 auto;
      }
    }
    /* 播放器 */
    .audio{
      position: absolute;
      top: 25px;
      width: 100%;
      background-color: #fff;
      z-index: 66;
      audio{
        width: 50%;
      }
    }
  }
}
/* #index .index_lunbo{
  margin-top: 200px;
} */
/* 占位 */
.zhanwei{
  width: 100px;
  height: 60px;
}
</style>
